* {
    margin: 0;
    box-sizing: border-box;
}
@import "./variable.scss";
@import "./mixin.scss";
@import "./default.scss";
.indexBannerFirst {
    background-image: url(../image/banner/banner1.png);
    background-size: cover;
    height: 650px;
    display: flex;
    padding: $main-layout;
    padding-top: 139px;
    .firstDiv {
        width: 42%;
        min-width: 432px;
        margin-right: 7.7vw;
    }
    .whiteLine {
        margin-top: 35px;
    }
    .content {
        margin-top: 15px;
    }
    .smallBtn {
        margin-top: 29px;
    }
    .font48px {
        line-height: 40px;
        margin-top: 21.5px;
    }
    .font72px {
        margin-top: 12px;
    }
}
.formTransform{
    transform: translateY(43px) ;
}
.formOne {
    position: relative;
    width: 587px;
    height: 610px;
    border: 1px solid rgba(0, 225, 255, 0.6);
    background: rgba(3, 102, 208, 0.6);
    backdrop-filter: blur(20px);
    display: flex;
    flex-flow: column;
    padding: 49px 58px;
    overflow: hidden;

    border-radius: 30px;
    h3 {
        font-family: "Poppins";
    }
    h2 {
        line-height: 40px;
    }
    p {
        line-height: 40px;
    }
    .marginBottom {
        margin-bottom: 20px;
    }
    input[type="checkbox"] {
        display: none;
    }
    .checkbox {
        height: 18px;
        width: 18px;
        background: #ffffff;
        border: 1px solid #b1b1b1;
        display: inline-block;
        cursor: pointer;
    }
    .newInfoDiv,
    .classInfoDiv {
        display: flex;
        align-items: center;
        cursor: pointer;
        .colorWhite{
            font-size: 15px;
            font-weight: 300;
            margin-left: 5px;
            cursor: pointer;
        }
    }
    .newInfoDiv{
        margin-top: 20px;
    }
    .classInfoDiv{
        margin-top: 5px;
    }
    input[type="text"] {
        &::placeholder {
            color: white;
            font-size: 15px;
            font-family: "Poppins";
            font-weight: 300;
        }
        padding: 10px 10px 10px 20px;
        margin-top: 20px;
        width: 471px;
        height: 40px;
        background: rgba(255, 255, 255, 0.3);
        box-shadow: 0px 10px 20px rgba(7, 7, 26, 0.15);
        border-radius: 10px;
        outline: none;
        border: 0;
    }
    .middleBtn{
        margin-top: 7px;
        letter-spacing: 3px;
        font-weight: 700;
        cursor: pointer;
    }
}
.indexBannerSecond{
    background-color: #181C47;
    padding-left: $main-layout-left;
    padding-top: 115px;
    padding-bottom: 40px;
    display: flex;
    .firstDiv{
        width: 470px;
        flex-shrink: 0;
    }
    .secondDiv{
        width: 569px;
        margin-left: 10vw;
        margin-top: 50px;
        img{
            width: 100%;
        }
    }
    .font20px{
        font-family: 'Poppins';
        padding-left: 25px;
        li{
            margin-bottom: 30px;
        }
    }
    .font24px{
        line-height: 32px;
        margin-bottom: 40px;
        font-family: 'Poppins';

    }
    h2{
        margin-bottom: 25px;
    }
}
.indexBannerThird{
    >h2{
        text-align: center;
        margin-top: 90px;
        margin-bottom: 30px;
    }
    >p{
        line-height: 40px;
        text-align: center;
        font-family: 'Noto Sans TC';
        width: 956pxx;
        margin: 0 auto;
    }
    .cardContainer{
        display: grid;
        grid-template-columns: 233px 233px 233px 233px;
        grid-gap: 21px 21px;
        justify-content: center;
        margin-top: 50px;
        margin-bottom: 90px;
    }
    .card{
        background-color: rgba(242, 153, 74, 0.19) ;
        display: inline-block;
        width: 233px;
        height: 270px;
        border-radius: 15px;
        text-align: center;
        .imgContainer{
            display: flex;
            justify-content: center;
            align-items: flex-end;
            height: 120px;
            margin-bottom: 26px;
        }
        h3{
            margin-bottom: 11px;
        }
        p{
            margin: 0 28px;
        }
    }

}
.indexBannerFourth{
    background-image: url("../image/banner/banner2.png");
    height: 528px;
    background-size: cover;
    background-position: center center;
}
.indexBannerFifth{
    > h2{
        margin-left: $main-layout-left;
        margin-top: 100px;
    }
    .imgContainer{
        text-align: center;
    }
    .listContainer{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        height: 144px;
    }
    .line{
        height: 1px;
        width: 81.5%;
        margin: 80px auto 120px auto;
        background-color: #E5E5E5;
    }
    .list{
        display: flex;
        align-items: center;
        p{
            margin-left: 25px;
            width: 300px;
            span{
                font-style: italic;
            }
        }
        &:nth-child(2){
            p{
                padding-right: 20px;
            }
        }
    }
    .bottleContainer{
        display: flex;
        justify-content: space-between;
        margin: $main-layout;
        >div{
            width: 443px;
            .imgContainer{
                text-align: center;
                margin-top: 25px;
                margin-bottom: 110px;
            }
        }
        p{
            line-height: 40px;
            span{
                font-style: italic;
            }
        }
    }
}
.indexBannerSixth{
    background-color: #0366D0;
    height: 674px;
    text-align: center;
    padding-top: 82px;
    h2{
        margin-bottom: 30px;
    }
}
